import React from 'react';
import Link from "gatsby-link";
import hobbits from "../pages/images/race_hobbit.jpg"


export default() =>
<div className="banner-race">
    <div className="jumbotron races-banner">
        <div className="container">
            <h1 className="text-center">Races</h1>
            <p>Whether as a stalwart defender of Gondor, or a fierce guardian of Dol-Guldur, deciding which race to play will define who you'll fight for in this neverending war.</p>
        </div>
    </div>
    <div className="container-fluid">
        <div className="row">
            <div className="col-lg-6">
                <h2 className="race-people">The Free People of Middle Earth</h2>
                <div className="row card-race">
                    <div className="col-lg-6">
                        <img src={hobbits} />
                    </div>
                    <div className="col-lg-6 card-race-content">
                        <h3 className="card-race-title">Hobbits</h3>
                        <p className="card-race-desc">Hobbits, also know as Halflings, are an ancient mortal race that live in Middle-Earth. Although their exact origins are unknown, they were initially found in the northern regions of Middle-Earth and below the Vales of Anduin.</p>
                    </div>
                </div>
            </div>
            <div className="col-lg-6">
                <h2 className="race-people">The Forces of the Shadow</h2>
            </div>
        </div>
    </div>
</div>


